<template>
  <van-tabbar
    v-show="display"
    :border="false"
    :z-index="10"
    class="GlobalBottomBar"
    safe-area-inset-bottom
    v-model="tabIndex"
  >
    <template v-for="item in cell">
      <template v-if="item.name === 'scan'">
        <van-tabbar-item :key="item.name" class="scan">
          <div slot="icon" class="scanBtn" @click="handlerScan"></div>
        </van-tabbar-item>
      </template>
      <template v-else>
        <van-tabbar-item
          v-if="item.available"
          :key="item.name"
          :class="{
            'van-tabbar-item--active': item.name === active,
            [active]: true,
          }"
          :icon="item.icon"
          :name="item.name"
          @click="
            () => {
              $router.replace(item.to);
            }
          "
        >
          {{ item.label }}
        </van-tabbar-item>
      </template>
    </template>
    <svg class="chatRoomBtn-svg" viewBox="0 0 750 98">
      <defs>
        <filter id="shadow" height="200%" width="200%" x="0" y="-100%">
          <feFlood flood-opacity="0.15" result="basic" />
          <feComposite
            in="SourceAlpha"
            in2="basic"
            operator="in"
            result="basic_color"
          />
          <feGaussianBlur in="basic_color" result="blur" stdDeviation="10" />
          <feOffset dx="0" dy="0" in="blur" result="offsetBlur" />
          <feMerge>
            <feMergeNode in="offsetBlur" />
            <feMergeNode in="SourceGraphic" />
          </feMerge>
        </filter>
      </defs>
      <path
        d="M556,1296c33.1-.87,50.842-22.39,58-31,7.7-9.44,21.891-30.02,41.127-30,138.95,0.14,255.873,0,255.873,0a20,20,0,0,1,20,20v78H556m0,0H181v-78a20,20,0,0,1,20-20s116.923,0.14,255.873,0c19.236-.02,33.43,20.56,41.127,30,7.158,8.61,24.9,30.13,58,31"
        filter="url(#shadow)"
        transform="translate(-181 -1235)"
      />
    </svg>
    <div class="ios-safe-area-cover"></div>
    <van-popup
      v-model="showScanPop"
      :style="{ height: '65%' }"
      class="sannerPopup"
      close-on-popstate
      get-container="body"
      position="bottom"
      round
      safe-area-inset-bottom
      @close="handlerPopupClose"
    >
      <template v-if="showScanPop">
        <div class="scanArea">
          <div id="reader"></div>
        </div>
        <div class="btns">
          <van-button class="start" type="primary" @click="startScan">
            {{ scanning ? "停止扫描" : "开始扫描" }}
          </van-button>
          <van-button class="image" type="info">
            扫描图片
            <input id="qr-input-file" accept="image/*" type="file" />
          </van-button>
        </div>
      </template>
    </van-popup>
  </van-tabbar>
</template>


<script>
import { Html5Qrcode } from "html5-qrcode";
import { Tabbar, TabbarItem } from "vant";

export default {
  name: "BottomBar",
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
  },
  data() {
    return {
      tabIndex: "",
      active: "index",
      showScanPop: false,
      scanning: false,
    };
  },
  computed: {
    display() {
      return this.$root.bottomBarDisplay;
    },
    cell() {
      return [
        {
          name: "index",
          label: "首页",
          to: "/",
          alias: [],
          icon: "wap-home",
          available: true,
        },
        {
          name: "transaction",
          label: "挂单",
          to: "/trade",
          alias: [
            "/trade/transaction",
            "/trade/crypto_trading",
            "/trade/earn_agreement",
          ],
          icon: "todo-list",
          available: true,
        },
        {
          name: "scan",
          label: "",
          to: "",
          alias: [],
          icon: "",
          available: true,
        },
        //
        {
          name: "orders",
          label: "订单",
          to: "/orders/list",
          alias: [],
          icon: "bill",
          available: true,
        },
        {
          name: "payments",
          label: "管理",
          to: "/payments",
          alias: [],
          icon: "card",
          available: true,
        },
      ];
    },
  },
  watch: {
    $route: {
      handler(to) {
        for (let item of this.cell) {
          if (item.to === to.path || item.alias.includes(to.path)) {
            this.tabIndex = item.name;
            this.active = item.name;
            break;
          } else {
            this.active = "";
          }
        }
      },
      immediate: true,
    },
  },
  created() {
    this.$root.$on(
      "root:close-app-scan-overlay",
      this.closeAppScanPopupOverlay
    );
  },
  methods: {
    closeAppScanPopupOverlay() {
      this.back && this.back();
      this.$root.app.appScanPopupOverlay = false;
    },
    handlerScan() {
      if (this.$root.isH5Plus) {
        this.$root.app.appScanPopupOverlay = true;
        this.back = window.h5p.startQrcodeScan((res) => {
          try {
            const qrInfo = JSON.parse(res);
            this.$root.app.appScanPopupOverlay = false;
            if (qrInfo.status) {
              this.$root.processQrcodeResult(qrInfo.result);
            } else {
              this.$toast.fail("扫码失败！");
            }
          } catch (error) {
            this.$toast.fail("扫码错误！");
          }
        });
      } else {
        this.showScanPop = true;
        this.$nextTick(() => {
          this.scanImage();
        });
      }
    },
    handlerPopupClose() {
      this.scanning = false;
      this.$emit("close");
      this.$nextTick(() => (this.showScanPop = false));
    },
    startScan() {
      if (this.scanning) {
        this.$emit("triggle");
        return;
      }
      this.$nextTick(() => {
        Html5Qrcode.getCameras()
          .then((devices) => {
            try {
              if (devices && devices.length > 0) {
                const deviceId = devices[devices.length - 1].id;
                const scanner = new Html5Qrcode(/* element id */ "reader");
                scanner
                  .start(
                    { deviceId: { exact: deviceId } },
                    { fps: 10, qrbox: { width: 160, height: 160 } },
                    (decodedText, decodedResult) => {
                      /* handle success */
                      this.$root.processQrcodeResult(decodedText);
                      this.handlerPopupClose();
                    },
                    (errorMessage) => {
                      // console.error(errorMessage);
                    }
                  )
                  .then(() => {
                    this.scanning = true;
                  })
                  .catch((err) => {
                    console.error(err);
                    this.$toast.fail("系统错误，唤起扫描失败");
                  });
                this.$once("triggle", () => {
                  if (this.scanning) {
                    try {
                      scanner && scanner.stop();
                    } catch (error) {}
                  }
                  this.scanning = !this.scanning;
                });
                this.$once("close", () => {
                  try {
                    scanner && scanner.stop();
                  } catch (error) {}
                });
                this.$once("hook:beforeDestroy", () => {
                  try {
                    scanner && scanner.stop();
                  } catch (error) {}
                });
              } else {
                this.$toast.fail("未检测到摄像头！");
              }
            } catch (error) {
              console.log(error);
            }
          })
          .catch((err) => {
            console.error(err);
            this.$toast.fail("无法获取摄像头权限！");
          });
      });
    },
    scanImage() {
      const scanner = new Html5Qrcode(/* element id */ "reader");
      const fileInput = document.getElementById("qr-input-file");
      fileInput.addEventListener("change", (e) => {
        if (e.target.files.length == 0) {
          return;
        }
        this.scanning = false;
        this.$emit("close");
        const imageFile = e.target.files[0];
        scanner
          .scanFile(imageFile, true)
          .then((decodedText) => {
            console.log(decodedText);
            this.handlerPopupClose();
            this.$root.processQrcodeResult(decodedText);
          })
          .catch((err) => {
            // failure, handle it.
            console.log(`Error scanning file. Reason: ${err}`);
          });
      });
    },
  },
};
</script>


<style lang="scss">
