    background-image: url("@/assets/icons/scan@3x.png");
    background-size: 24px 24px;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    object-fit: cover;
  }

  // &.disChatRoom {
  //   border-radius: 10px 10px 0 0;
  //   box-shadow: 0 0 15px rgba(0, 0, 0, 0.08);
  //   border-top: 1px solid rgba(0, 0, 0, 0.07);
  //   .van-tabbar-item {
  //     flex: 0 0 25%;
  //     background: #fff;
  //   }
  //   .svg {
  //     width: 20px;
  //     height: 18px;
  //   }
  // }
  .chatRoomBtn-svg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: visible;
    fill: #fff;
  }

  .ios-safe-area-cover {
